    export default {
        name: "collection",
        data() {
            return {
                statusHeight:0,
                checkCount:0,
                navigationHeight:0,
                StatusBar: this.StatusBar,
                CustomBar: this.CustomBar,
                dialog: "",
                switchA: false,
                isWeixin: false,
                topCount:0,
                modalName: false,
                bottomName: false,
                choose: 0,
                updateCollectionDataUrl:"",
                list:[
                    {book:{bookPic:"",bookName:"",bookReading:"",bookBytime:""}, id:"",top:false,check: false},
                    {book:{bookPic:"",bookName:"",bookReading:"",bookBytime:""}, id:"",top:false,check: false},
                    {book:{bookPic:"",bookName:"",bookReading:"",bookBytime:""}, id:"",top:false,check: false},
                    {book:{bookPic:"",bookName:"",bookReading:"",bookBytime:""}, id:"",top:false,check: false},
                    {book:{bookPic:"",bookName:"",bookReading:"",bookBytime:""}, id:"",top:false,check: false},
                    {book:{bookPic:"",bookName:"",bookReading:"",bookBytime:""}, id:"",top:false,check: false},
                    {book:{bookPic:"",bookName:"",bookReading:"",bookBytime:""}, id:"",top:false,check: false},
                ],
                checkbox: [{
                    value: 'A',
                    checked: true
                }, {
                    value: 'B',
                    checked: true
                }, {
                    value: 'C',
                    checked: false
                }],
                isShow: false
            };
        },
        computed: {
            style() {
                var StatusBar = this.StatusBar;
                var CustomBar = this.CustomBar;
                var bgImage = this.bgImage;
                var style = `height:${CustomBar}px;padding-top:${StatusBar}px;`;
                if (this.bgImage) {
                    style = `${style}background-image:url(${bgImage});`;
                }
                return style
            }
        },
		onLoad() {
            this.statusHeight = uni.getSystemInfoSync().statusBarHeight;
            this.navigationHeight = uni.getSystemInfoSync().navigationBarHeight;



            uni.showLoading({
                title: '加载中'
            });
            this.refresh();
		},
        // 下拉刷新响应
        onPullDownRefresh() {
            this.refresh();
        },
        methods: {
            BackPage() {
                if (getCurrentPages().length < 2 && 'undefined' !== typeof __wxConfig) {
                    let url = '/' + __wxConfig.pages[0];
                    return uni.redirectTo({url})
                }
                uni.navigateBack({
                    delta: 1
                });
            },
            CheckboxChange(e) {
                var items = this.checkbox,
                    values = e.detail.value;
                for (var i = 0, lenI = items.length; i < lenI; ++i) {
                    items[i].checked = false;
                    for (var j = 0, lenJ = values.length; j < lenJ; ++j) {
                        if (items[i].value === values[j]) {
                            items[i].checked = true;
                            break
                        }
                    }
                }
            },
            refresh() {
                var collection = this;
                uni.showLoading({
                    mask: true
                });
                uni.request({
                    url:this.serverUrl+"/book/getSave?userId=" + this.userId,
                    method:"GET",
                    success(res) {
                        collection.list = res.data.data;
                        for (let i = 0; i < collection.list.length; i++) {
                            collection.list[i].check = false;
                            collection.list[i].top = collection.list[i].top === 1
                        }
						collection.topCount = 0;
						collection.switchA = false;
                        collection.sortByTop();
                    },
                    fail() {

                    },
                    complete() {
                        uni.stopPullDownRefresh();
                        uni.hideLoading();
                    }
                })
            },
            CheckBoxIsShow(e) {
                this.isShow = !this.isShow;
            },
            SwitchA(e) {
                this.switchA = !this.switchA;
                for (let i = 0; i < this.list.length; i++) {
                    this.list[i].check = this.switchA;
                }
            },
            Delete() {
                let data = [];
                for (let i = 0; i < this.list.length; i++) {
                    if (this.list[i].check) {
                        data.push({"id":this.list[i].id});
                        this.list.splice(i,1)
                    }
                }
                uni.request({
                	url: this.serverUrl + "/book/deleteSaves",
					method:"POST",
					data:data,
					success() {

					},
					fail() {

					},
					complete() {

					}
                })
            },
            sortByTop() {
                for (let i = 0; i < this.list.length; i++) {
                    if (this.list[i].top) {
                        let temp = this.list[i];
                        this.list.splice(i,1);
                        this.list.splice(0, 0, temp)
                    }
                }
            },
            Top() {
                let data = [];
                var check = 0;
                var tmp = true;
                this.topCount = 0;
                for (let i = 0; i <this.list.length; i++) {
                    if (this.list[i].check) {
                        data.push({"id":this.list[i].id});
                        check = check+1;
                        if (this.list[i].top) {
                            this.topCount = this.topCount + 1;
                        }
                    }
                }
                for (let i = 0; i < this.list.length; i++) {
                    if (this.list[i].check) {
                        if (check>1) {
                            if (this.topCount === check) {
                                tmp = false;
                            }
                            this.list[i].top = tmp;
                        } else {
                            this.list[i].top = !this.list[i].top;
                        }
                    }
                }
                this.sortByTop();
                uni.request({
                	url:this.serverUrl + "/book/changeSaveTops",
					method:"POST",
					data:data,
					success() {

					},
					fail() {

					},
					complete() {

					}
                })
            },
            Check(e) {
                var index = e.target.dataset.index;
                this.list[index].check = !this.list[index].check;
				if (this.list[index].check) {
					this.checkCount++;
				} else {
					this.checkCount--;
				}
                this.switchA = (this.checkCount === this.list.length);
            },
            c() {
                let data = [];
                for (let i = 0; i < this.list.length; i++) {
                    if (this.list[i].check) {
                        data.push(this.list[i])
                    }
                }
                return data;
            },
            showModal(e) {
                let data = this.c();
                this.dialog = (data.length === 0 ? "未选择图书" : "是否删除选中图书?");
                this.modalName = true;
            },
            hideModal(e) {
                this.modalName = false;
            },
            showBottomModal(i) {
                this.choose = i;
                console.log(this.choose);
                this.bottomName = true;
            },
            hideBottomModal(e) {
                this.bottomName = false;
            },
            turnDetail() {
                uni.navigateTo({
                    url:'?bookId=' + this.choose.bookId
                })
            },
            BottomTop() {
                this.list[this.choose].check = !this.list[this.choose].check;
                this.Top()
            },
            BottomDelete() {
                this.list[this.choose].check = !this.list[this.choose].check;
                this.Delete()
            },
            Share() {
                uni.showShareMenu()
            },

        }
    }
